import React from 'react';
function WindowTitle() {
    return (
        <div className="window-title">
            <div className="dots">
                <i className="fa fa-circle"></i>
                <i className="fa fa-circle"></i>
                <i className="fa fa-circle"></i>
            </div>
            <div className="title">
                <span>Чат</span>
            </div>
            <div className="expand">
                <i className="fa fa-expand"></i>
            </div>
        </div>
    );
}
export default WindowTitle;
